import {NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes : Routes = [
    {
        path : 'onBoard',
        loadChildren : () => import('./vehicle-onBoard/vehicle-onBoard.module').then(m=>m.VehicleOnBoardModule),
        data : {title : 'On Board'}
    },
    // {
    //     path : 'onBoard/create',
    //     loadChildren : () => import('./vehicle-onBoard/vehicle-onBoard.module').then(m=>m.VehicleOnBoardModule),
    //     data : {title : 'On Board'}
    // }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class VehicleRoutingModule{}